/* Header CSS */
:root {
  --header-height: 60px;
  --primary-color: #3498db;
  --secondary-color: #2980b9;
  --text-color: #333;
  --bg-color: #fff;
  --border-color: #eaeaea;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

/* Dark mode variables */
.dark-mode {
  --text-color: #f1f1f1;
  --bg-color: #121212;
  --border-color: #2a2a2a;
  --shadow-color: rgba(0, 0, 0, 0.3);
}

/* Global styles */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: var(--text-color);
  background-color: var(--bg-color);
  transition: var(--transition);
}

* {
  box-sizing: border-box;
}

/* Header styles */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--header-height);
  background-color: var(--bg-color);
  box-shadow: 0 1px 8px var(--shadow-color);
  z-index: 1000;
  transition: var(--transition);
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding: 0 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.header-left, .header-right {
  display: flex;
  align-items: center;
  position: relative;
}

/* Menu button styles */
.menu-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Menu styles */
.nav-menu {
  position: absolute;
  top: var(--header-height);
  left: -300px;
  width: 250px;
  background-color: var(--bg-color);
  box-shadow: 2px 2px 10px var(--shadow-color);
  border-radius: 0 0 5px 0;
  transition: var(--transition);
  z-index: 1001;
  opacity: 0;
  visibility: hidden;
}

.nav-menu.open {
  left: 0;
  opacity: 1;
  visibility: visible;
}

.nav-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-menu li {
  border-bottom: 1px solid var(--border-color);
}

.nav-menu li:last-child {
  border-bottom: none;
}

.nav-menu a {
  display: block;
  padding: 15px 20px;
  color: var(--text-color);
  text-decoration: none;
  transition: var(--transition);
}

.nav-menu a:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

/* Dark mode toggle styles */
.dark-mode-toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  cursor: pointer;
}

.toggle {
  position: relative;
  width: 40px;
  height: 20px;
  background-color: #ccc;
  border-radius: 20px;
  transition: var(--transition);
  border: none;
  cursor: pointer;
}

.toggle.active {
  background-color: var(--primary-color);
}

.toggle-thumb {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  background-color: white;
  border-radius: 50%;
  transition: var(--transition);
}

.toggle.active .toggle-thumb {
  left: 22px;
}

/* Search styles */
.search-container1 {
  display: flex;
  align-items: center;
  width: 55% !important; 
  height: 40px !important;
  justify-content: center;
  max-width: 400px; /* Reduce max width */
  margin-bottom: 30px !important;
  padding: 5px; /* Reduce padding */
  border-radius: 20px; /* Slightly smaller border-radius */
  border: 1px solid #ddd;
  background: #fff;
}

.search-input1 {
  flex: 1;
  border: none;
  outline: none;
  padding: 5px; 
}

.search-icon1 {
  margin: 0 8px; /* Reduce margin */
  font-size: 16px; /* Adjust icon size if needed */
  color: #666;
}


/* Notification styles */
.notification-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.notification-badge {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #e74c3c;
  color: white;
  font-size: 10px;
  font-weight: bold;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px;
}

/* Notifications dropdown styles */
.notifications-dropdown {
  position: absolute;
  top: var(--header-height);
  right: -300px;
  width: 300px;
  background-color: var(--bg-color);
  box-shadow: 0 2px 10px var(--shadow-color);
  border-radius: 5px;
  transition: var(--transition);
  z-index: 1001;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
}

.notifications-dropdown.open {
  right: 0;
  opacity: 1;
  visibility: visible;
}

.notifications-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 15px;
  border-bottom: 1px solid var(--border-color);
}

.notifications-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.mark-all-read {
  background: none;
  border: none;
  color: var(--primary-color);
  font-size: 12px;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
  transition: var(--transition);
}

.mark-all-read:hover {
  background-color: rgba(52, 152, 219, 0.1);
}

.notifications-list {
  max-height: 300px;
  overflow-y: auto;
  padding: 0;
  margin: 0;
  list-style: none;
}

.notification-item {
  display: flex;
  align-items: flex-start;
  padding: 12px 15px;
  border-bottom: 1px solid var(--border-color);
  transition: var(--transition);
}

.notification-item:hover {
  background-color: rgba(0, 0, 0, 0.02);
}

.notification-icon {
  margin-right: 12px;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #95a5a6;
}

.notification-icon.unread {
  color: var(--primary-color);
}

.notification-content {
  flex-grow: 1;
}

.notification-text {
  margin: 0 0 4px 0;
  font-size: 14px;
  color: var(--text-color);
}

.notification-time {
  font-size: 12px;
  color: #95a5a6;
}

.notifications-footer {
  padding: 12px 15px;
  text-align: center;
  border-top: 1px solid var(--border-color);
}

.notifications-footer a {
  color: var(--primary-color);
  text-decoration: none;
  font-size: 14px;
  transition: var(--transition);
}

.notifications-footer a:hover {
  text-decoration: underline;
}

/* Icon styles */
.icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
  color: var(--text-color);
}

/* Responsive styles */
@media screen and (max-width: 768px) {
 
  
  .notifications-dropdown {
    width: 280px;
  }
}

@media screen and (max-width: 576px) {
  .header-container {
    padding: 0 10px;
  }
  

  
  .notifications-dropdown {
    width: 100%;
    position: fixed;
    left: 0;
    right: 0;
    border-radius: 0;
  }
  
 
}

/* Dark/Light mode specific styles */
.header.dark {
  background-color: var(--bg-color);
  border-bottom: 1px solid var(--border-color);
}

.header.dark .icon {
  color: var(--text-color);
}

.header.dark .search-input {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: var(--border-color);
}

/* Animation for menus */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.nav-menu.open, .notifications-dropdown.open {
  animation: fadeIn 0.2s ease-out;
}