:root {
  --primary-color: #4361ee;
  --secondary-color: #3f37c9;
  --background: #f8f9fa;
  --surface: #ffffff;
  --text-primary: #2b2d42;
  --text-secondary: #8d99ae;
  --accent: #4cc9f0;
  --error: #e63946;
  --success: #2a9d8f;
  --border-radius: 16px; /* Increased for better rounding */
  --box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Base Styles */
.community-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 1rem;
  font-family: 'Segoe UI', Roboto, sans-serif;
  background-color: var(--background);
  min-height: 100vh;
}

/* Header Styles */
header {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  margin-bottom: 1.5rem;
}

.header-actions {
  display: flex;
  gap: 0.5rem;
}

button {
  transition: all 0.2s ease;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  padding: 0.5rem 1rem; /* Reduced size */
  border-radius: var(--border-radius);
  border: 1px solid transparent;
  font-weight: 500;
}

.header-actions button {
  background-color: var(--primary-color);
  color: white;
  border: none;
}

.header-actions button.refresh-button {
  background-color: var(--success);
}

/* View Modes */
.view-modes {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.view-modes button {
  background: var(--surface);
  border: 1px solid #e0e0e0;
  color: var(--text-primary);
  box-shadow: var(--box-shadow);
}

.view-modes button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}

/* Search and Filter */
.search-filter-container {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  align-items: center;
  margin-bottom: 1.5rem;
}

.search-bar {
  flex: 1;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid #ccc;
  box-shadow: var(--box-shadow);
  font-size: 1rem;
}

.filter-button {
  background: var(--surface);
  color: var(--text-primary);
  border: 1px solid #e0e0e0;
  padding: 0.4rem 0.75rem;
  font-size: 0.9rem;
  border-radius: 8px;
}

.filter-button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}

/* Questions Main */
.questions-main {
  margin-top: 2rem;
}

.questions-header {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.question-card {
  background: var(--surface);
  padding: 1.25rem;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
margin-bottom: 10px;
  transition: transform 0.2s ease;
}

.question-card:hover {
}

.vote-button {
  padding: 0.3rem 0.6rem; /* Smaller size */
  font-size: 10px;
  border-radius: 6px;
}

.load-more, .no-results button {
  width: 100%;
  background-color: var(--primary-color);
}

/* Interactive States */
button:hover {
  opacity: 0.9;
  transform: translateY(-1px);
}

button:active {
  transform: translateY(0);
}

/* Responsive Design */
@media (max-width: 768px) {
  .community-container {
    padding: 0.75rem;
  }

  header {
    flex-direction: column;
    align-items: stretch;
  }

  .header-actions {
    justify-content: space-between;
  }

  .header-actions button {
    flex: 1;
    justify-content: center;
  }

  .trending-grid {
    grid-template-columns: 1fr;
  }

  .search-filter-container {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  button {
    padding: 0.5rem 0.75rem;
    font-size: 0.75rem;
  }

  .active-filters {
    padding: 0.75rem;
    margin-right: 5px;
  }
}
