.scrolling-container {
    overflow: hidden;
    padding: 20px;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .scrolling-wrapper {
    display: flex;
    gap: 20px;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    width: 100%;
    max-width: 400px;
    padding-bottom: 10px;
  }
  
  .scrolling-card {
    min-width: 100%;
    height: 250px;
    background-size: cover;
    background-position: center;
    border-radius: 15px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    position: relative;
    scroll-snap-align: center;
  }
  
  .card-overlay {
    background: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 20px;
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    border-radius: 0 0 15px 15px;
  }
  
  .dots-navigation {
    display: flex;
    gap: 5px;
    margin-top: 10px;
  }
  
  .dot {
    width: 10px;
    height: 10px;
    background: lightgray;
    border-radius: 50%;
    transition: background 0.3s;
  }
  
  .dot.active {
    background: black;
  }
  
  @media (min-width: 768px) {
    .scrolling-wrapper {
      max-width: 500px;
    }
  }