/* lio.css */

:root {
    --primary-color: #6366f1;
    --secondary-color: #4f46e5;
    --background-color: #f8fafc;
    --card-bg: #ffffff;
    --text-color: #1e293b;
    --text-secondary: #64748b;
    --success-color: #10b981;
    --error-color: #ef4444;
    --border-color: #e2e8f0;
    --border-radius: 8px;
    --shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
    color: var(--text-color);
    background-color: var(--background-color);
    line-height: 1.5;
  }
  
  .lio-container {
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-template-columns: auto 1fr;
    grid-template-areas:
      "nav nav"
      "sidebar main"
      "input input";
    height: 100vh;
    max-height: 100vh;
    overflow: hidden;
  }
  
  /* Navigation Styles */
  .navigation {
    grid-area: nav;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem 1rem;
    background-color: var(--card-bg);
    border-bottom: 1px solid var(--border-color);
    box-shadow: var(--shadow);
    z-index: 10;
  }
  
  .nav-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: var(--border-radius);
    color: var(--text-color);
  }
  
  .nav-button:hover {
    background-color: var(--background-color);
  }
  
  .nav-actions {
    display: flex;
    gap: 0.5rem;
  }
  
  /* Sidebar Styles */
  .sidebar {
    grid-area: sidebar;
    background-color: var(--card-bg);
    border-right: 1px solid var(--border-color);
    width: 280px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    position: fixed;
    height: calc(100vh - 56px);
    top: 56px;
    z-index: 5;
  }
  
  .sidebar.open {
    transform: translateX(0);
  }
  
  .sidebar-header {
    padding: 1rem;
    border-bottom: 1px solid var(--border-color);
  }
  
  .search-container {
    display: flex;
    align-items: center;
    background-color: var(--background-color);
    padding: 0.5rem;
    border-radius: var(--border-radius);
    margin-bottom: 1rem;
  }
  
  .search-container input {
    background: none;
    border: none;
    outline: none;
    font-size: 0.875rem;
    margin-left: 0.5rem;
    width: 100%;
  }
  
  .sidebar-actions {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .sidebar-button {
    padding: 0.5rem;
    background-color: var(--primary-color);
    color: white;
    border: none;
    border-radius: var(--border-radius);
    cursor: pointer;
    font-size: 0.875rem;
  }
  
  .sidebar-button.temp-chat {
    background-color: var(--background-color);
    color: var(--text-color);
    border: 1px solid var(--border-color);
  }
  
  .chats-list {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    padding: 0.5rem;
    overflow-y: auto;
  }
  
  .chat-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.75rem;
    border-radius: var(--border-radius);
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .chat-item:hover {
    background-color: var(--background-color);
  }
  
  .chat-item.active {
    background-color: rgba(99, 102, 241, 0.1);
    border-left: 3px solid var(--primary-color);
  }
  
  .chat-info {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    width: calc(100% - 60px);
  }
  
  .chat-title {
    font-size: 0.875rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .chat-actions {
    display: flex;
    gap: 0.5rem;
    opacity: 0;
    transition: opacity 0.2s;
  }
  
  .chat-item:hover .chat-actions {
    opacity: 1;
  }
  
  .chat-actions button {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--text-secondary);
  }
  
  .chat-actions button:hover {
    color: var(--text-color);
  }
  
  .chat-rename-container {
    display: flex;
    width: 100%;
    gap: 0.5rem;
  }
  
  .chat-rename-container input {
    flex: 1;
    padding: 0.25rem 0.5rem;
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius);
    font-size: 0.875rem;
  }
  
  .temp-chat {
    opacity: 0.7;
  }
  
  .temp-icon {
    color: var(--text-secondary);
  }
  
  /* Chat Container */
  .chat-container {
    grid-area: main;
    padding: 1rem;overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-left: 0;
    transition: margin-left 0.3s ease;
  }
  
  @media (min-width: 768px) {
    .sidebar {
      position: relative;
      transform: none;
      top: 0;
      height: calc(100vh - 56px);
    }
    
    .sidebar:not(.open) {
      width: 0;
      min-width: 0;
      overflow: hidden;
    }
    
    .chat-container {
      margin-left: 0;
    }
  }
  
  .message {
    display: flex;
    gap: 1rem;
    padding: 1rem;
    border-radius: var(--border-radius);
    max-width: 90%;
    animation: fadeIn 0.3s ease;
  }
  
  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
  }
  
  .user-message {
    background-color: rgba(99, 102, 241, 0.1);
    margin-left: auto;
    flex-direction: row-reverse;
  }
  
  .assistant-message {
    background-color: var(--card-bg);
    border: 1px solid var(--border-color);
    margin-right: auto;
  }
  
  .message-content {
    white-space: pre-wrap;
    word-break: break-word;
  }
  
  .bot-icon, .user-icon {
    flex-shrink: 0;
    color: var(--primary-color);
  }
  
  .empty-chat {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: var(--text-secondary);
    text-align: center;
    padding: 1rem;
  }
  
  .empty-chat h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: var(--text-color);
  }
  
  .loading-container {
    display: flex;
    justify-content: center;
    padding: 1rem;
  }
  
  .voice-indicator {
    display: flex;
    align-items: center;
    gap: 0.25rem;
  }
  
  .voice-dot {
    width: 8px;
    height: 8px;
    background-color: var(--primary-color);
    border-radius: 50%;
    animation: pulse 1.5s infinite;
  }
  
  .voice-dot:nth-child(2) {
    animation-delay: 0.5s;
  }
  
  .voice-dot:nth-child(3) {
    animation-delay: 1s;
  }
  
  @keyframes pulse {
    0%, 100% { transform: scale(0.8); opacity: 0.5; }
    50% { transform: scale(1.2); opacity: 1; }
  }
  
  .voice-indicator.speaking svg {
    animation: speakPulse 1s infinite;
  }
  
  @keyframes speakPulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.1); }
  }
  
  /* Voice Mode */
  .voice-mode {
    position: fixed;
    bottom: 80px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--card-bg);
    padding: 1rem;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    width: 90%;
    max-width: 500px;
    z-index: 10;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .voice-status {
    display: flex;
    justify-content: center;
    height: 30px;
  }
  
  .transcript {
    text-align: center;
    color: var(--text-secondary);
    font-size: 0.875rem;
    min-height: 20px;
  }
  
  /* Chat Input */
  .chat-input {
    grid-area: input;
    padding: 1rem;
    background-color: var(--card-bg);
    border-top: 1px solid var(--border-color);
  }
  
  .input-container {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    max-width: 900px;
    margin: 0 auto;
  }
  
  .input-container textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius);
    resize: none;
    min-height: 60px;
    max-height: 150px;
    font-family: inherit;
    font-size: 1rem;
  }
  
  .input-container textarea:focus {
    outline: none;
    border-color: var(--primary-color);
  }
  
  .input-actions {
    display: flex;
    justify-content: space-between;
  }
  
  .voice-button, .send-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem 1rem;
    background-color: var(--primary-color);
    color: white;
    border: none;
    border-radius: var(--border-radius);
    cursor: pointer;
    font-size: 0.875rem;
    transition: background-color 0.2s;
  }
  
  .voice-button {
    background-color: var(--background-color);
    color: var(--text-color);
    border: 1px solid var(--border-color);
  }
  
  .voice-button:hover {
    background-color: var(--border-color);
  }
  
  .voice-button .listening {
    color: var(--success-color);
    animation: pulse 1.5s infinite;
  }
  
  .send-button:hover {
    background-color: var(--secondary-color);
  }
  
  .send-button:disabled {
    background-color: var(--border-color);
    cursor: not-allowed;
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .message {
      max-width: 100%;
    }
  }